<template>
  <view class="apply-container">
    <view class="form-container">
      <TrainApplyForm v-bind="{ formData: form, readonly }" ref="trainApplyForm"></TrainApplyForm>
    </view>
  </view>
</template>
<script>
import TrainApplyForm from '@/pages-sub/trainApply/form.vue'
export default {
  components: {
    TrainApplyForm,
  },
  data() {
    return {
      type: 3,
      readonly: true,
      form: {
        name: '',
      },
    }
  },
  onLoad() {
    this.getDetail()
  },
  methods: {
    getDetail() {
      let form = this.$store.user.userInfo.instructor_info?.rs || {}
      form = {
        ...form,
        title: `${form.province_str}${form.city_str}${form.county_str}${form.promote_level_str || ''}`,
        avatar: form.avatar_str || form.avatar,
        tagIdsStr: (form.tag_ids_arr || [])
          .map((item) => {
            return item.tag_id_str
          })
          .join(','),
        tagGroup: `${form.province_str}/${form.city_str}/${form.county_str}`,
        nationStr: form.nation_str,
        educationLevelStr: form.education_level_str,
        levelStr: form.level_str,
        guide_type: form.guide_type,
        guideTypeStr: form.guide_type_str,
        personnelFormStr: form.personnel_form_str,
        time:
          form.first_become_time == 0
            ? ''
            : this.$dayjs(form.first_become_time * 1000).format('YYYY-MM-DD'),
        now_level_organ_unit_name: form.now_level_organ_unit_name,
        now_level_grant_time:
          form.now_level_grant_time == 0
            ? ''
            : this.$dayjs(form.now_level_grant_time * 1000).format('YYYY-MM-DD'),
        status_str: form.status_str || this.$dict.getDictLabel('statusColor', form.status, {}),
        areaRangeStr: form.area_range_str,
      }
      console.log(form)
      this.form = form
    },
  },
}
</script>

